.container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding: 20px 0px;

  @media screen and (max-width: 540px) {
    padding: 20px 15px;
  }
  @media screen and (max-width: 760px) {
    padding: 25px 25px;
  }
  @media screen and (min-width: 760px) {
    padding: 25px 15px;
  }
}
